@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAFAFA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#app {
  /* display: grid; */
  /* grid-template-rows: 50px 1fr; */
  /* grid-template-columns: 1fr 1fr 1fr; */
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  width:100%;
  background-color:#FAFAFA;
}

#ImageForm{
  padding-top:100px;
  grid-column:2;
  margin:auto;
}

#signIn{
  grid-column: 2;
  margin:auto;
}

#signUp{
  grid-column: 2;
  margin:auto;
}

#posts{
  grid-column: 2;
}

#yourNote{
  grid-column: 2;
}

#homepage{
  margin-top:100px;
  grid-column: 2;
}

#signIn{
    float:left;
    margin-top:50px;    
}

#signIn > form{
    display:grid;
    grid-template-rows: 1fr 1fr 1fr 1fr;
}

.signInField{
    height:30px;
    font-size: 16px;
    margin-top:10px;
    border: 2px solid #ffbe76;
    text-decoration: none;
    padding-left:5px;
}

#signInButton{
    grid-row: 4;
    text-decoration: none;
    background-color: #ffbe76; 
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    color: #34495e;
    font-weight: bold;
    font-size: 14px;

}

#signInButton:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);

}


#signUp{
    float:left;
    margin-top:50px;
}

#signUp > form{
    display:grid;
    grid-template-rows: 1fr 1fr 1fr 1fr;
}

.signUpField{
    height:30px;
    font-size: 16px;
    margin-top:10px;
    border: 2px solid #ffbe76;
    text-decoration: none;
    padding-left:5px;
}

#signUpButton{
    grid-row: 4;
    text-decoration: none;
    background-color: #ffbe76; 
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    color: #34495e;
    font-weight: bold;
    font-size: 14px;

}

#signUpButton:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);

}
#posts{
    padding-top:50px;
    display:grid;
    grid-template-columns: 1fr 1fr 1fr;
}
#thumbnail{
    width:600px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin-bottom:20px;
  margin-top:20px;
  grid-column: 2;
}

#thumbnail:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

#title{
    width:100%;
    color: gray;
}

 h1{
    text-align: left;
    text-decoration: underline;
    /* background-color:#34495e; */
    color:#FAFAFA;
    padding-top:2px;
    padding-left:5px;
    padding-bottom:2px;
}

.header{
    display:grid;
    grid-template-columns: 1fr 80px 80px;
    background-color:#34495e;
}
.delete{
    text-align:right;
    grid-column: 2;
    width:50px;
    height:20px;
    margin:auto;
    font-size:12px;
    grid-row:1;
}

.seeNote{
    text-align:right;
    grid-column: 3;
    width:70px;
    height:20px;
    margin:auto;
    font-size:12px;
    grid-row: 1;
}

@media only screen and (max-width: 600px) {

    #thumbnail{
        width:350px;
    }
}

#ImageForm{
    display:grid;
    grid-template-rows: 1fr 80px 20px 50px 300px;
    /* grid-column: 2; */
    grid-row:2;
    
}

#file-input{
    grid-row:3;
    margin:auto;

}

#directions{
    grid-column: 1;
    margin:auto;
    text-align: center;
}
#title{
    grid-row:4;
    margin:auto;

    width:350px;

    height:30px;
    font-size: 16px;
    margin-top:10px;
    border: 2px solid #ffbe76;
    text-decoration: none;
    padding-left:5px;
}
#editThis{
    grid-row:5;
    width:500px;
    height:300px;
    margin:auto;
    font-size: 16px;
    border: 2px solid #ffbe76;
    text-decoration: none;
    padding-left:5px;
}

#saveText{
    padding-top:10px;
    margin:auto;
    font-size: 24px;
    color:#34495e;
    grid-row:2;
    width:200px;
    height: 50px;
    font-weight: bold;
    background-color: #ffbe76; /* Green */
    border: none;
    padding: 5px 10px;
    text-align: center;
    border-radius: 3px;
    text-decoration: none;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    /* display: inline-block; */
    /* font-size: 16px; */

}

#saveText:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);

}

#loadingIndicator{
    margin:auto;
}

@media only screen and (max-width: 600px) {

    #directions > h1{
        font-size: 20px;
    }
    #directions > p{
        font-size: 12px;
    }

    #editThis{
        width:300px;
    }

    #title{
        width:300px;
    }
    #saveText{
        width:150px;
    }
}
#NavBar{
    width:100%;
    height:50px;
    background-color:#ffbe76;
    position: fixed;
    display:grid;
    grid-template-columns: 200px 1fr 500px;
}
@font-face {
    font-family: FunSized;
    src: url(note-hero.com/static/media/FunSized.95add1c9.ttf);
}
#titleNav{
    margin:auto;
    /* font-family: 'Roboto', sans-serif; */
    font-family: FunSized, sans-serif;

    grid-column:1;
}

#options{
    margin:auto;
    grid-column:3;
}

.navLink{
    margin-left:5px;
    text-decoration: none;
    background-color:#34495e;
    padding:3px;
    color:#FAFAFA;
    border-radius: 2px;
}

#signoutBtn{
    margin-left:5px;
    text-decoration: none;
    background-color:#e74c3c;
    padding:3px;
    color:#FAFAFA;
    border-radius: 2px;
    font-size: 16px;
}

#home{
    text-decoration: none;
    color: black;
    margin:auto;
}

@media only screen and (max-width: 600px) {
    #NavBar{
        width:100%;
        height:40px;
        background-color:#ffbe76;
        position: fixed;
        display:grid;
        grid-template-columns: 100px 1fr 200px;
    }

    .navLink{
        font-size: 10px;
        margin-right:0px;
    }
    #signoutBtn{
        font-size: 10px;
        margin-right:0px;
    }
  }
#yourNote{
    padding-top:100px;
}

#displayText{
    width:700px;
    margin:auto;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  padding: 10px;
}

#editText{
    width:700px;
    margin:auto;
}

#editText > textarea{
    width:700px;
    height: 700px;
}

#noteTitle{
    text-align: left;
    text-decoration: underline;
    /* background-color:#34495e; */
    color:black;
    padding-top:2px;
    padding-left:5px;
    padding-bottom:2px;
}

.noteButton{
    text-decoration: none;
    background-color: #ffbe76; 
    height:30px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    color: #34495e;
    font-weight: bold;
    font-size: 18px;
    margin: 10px;

}

.noteButton:hover{
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);

}

@media only screen and (max-width: 600px) {

    #displayText{
        width:300px;
       
      padding: 5px;
    }
    
    #editText{
        width:300px;
        margin:auto;
    }
    
    #editText > textarea{
        width:300px;
        height: 700px;
    }
}
@media only screen and (max-width: 600px) {
    #homepage > img{
        width:200px;
    }
}
